const Teams = () => {
  return (
    <section className="py-16 px-5 lg:px-12 xl:px-16 2xl:px-40">
      <h1 className="text-5xl md:text-7xl text-white text-center">The Team</h1>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-10 mt-16">
        {teamList.map((team) => (
          <div
            key={team.id}
            className="bg-black rounded-lg transition duration-500 ease-in-out transform hover:scale-110"
          >
            <div className={`${team.robotImg}`}>
              <div className="black-overlay hover-overlay text-center">
                <p className="text-gray-400 mt-60 font-simple font-semibold tracking-wider uppercase">
                  {team.position}
                </p>
                <h1 className="text-2xl text-white font-simple font-semibold mt-5">
                  {team.name}
                </h1>
                <a
                  href={team.btn1Link}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <button className="border-2 border-gray-400 mt-10 w-[8rem] py-2 rounded-md text-white font-simple transition duration-500 ease-in-out hover:bg-[#c2af58] hover:text-black hover:border-black">
                    {team.btn1}
                  </button>
                </a>
                <a
                  href={team.btn2Link}
                  className="block"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <button className="border-2 border-gray-500 mt-3 w-[8rem] py-2 rounded-md text-white font-simple transition duration-500 ease-in-out hover:bg-[#c2af58] hover:text-black hover:border-black">
                    {team.btn2}
                  </button>
                </a>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Teams;
