import Particles from "react-particles-js";

const SimpleParticles = () => {
  return (
    <>
      <div className="hidden 2xl:block">
        <Particles
          style={{ position: "absolute" }}
          height="100%"
          width="100%"
          params={{
            particles: {
              color: {
                value: "#c2af58",
              },
              links: {
                color: "#c2af58",
                distance: 130,
                enable: true,
                opacity: 0.8,
                width: 1,
              },
              number: {
                value: 100,
              },
              size: {
                value: 3,
              },
            },
          }}
        />
      </div>

      <div className="hidden lg:block 2xl:hidden">
        <Particles
          style={{ position: "absolute" }}
          height="100%"
          width="100%"
          params={{
            particles: {
              color: {
                value: "#c2af58",
              },
              links: {
                color: "#c2af58",
                distance: 100,
                enable: true,
                opacity: 0.8,
                width: 1,
              },
              number: {
                value: 60,
              },
              size: {
                value: 3,
              },
            },
          }}
        />
      </div>
      <div className="lg:hidden">
        <Particles
          style={{ position: "absolute" }}
          height="100%"
          width="100%"
          params={{
            particles: {
              color: {
                value: "#c2af58",
              },
              links: {
                color: "#c2af58",
                distance: 100,
                enable: true,
                opacity: 0.8,
                width: 1,
              },
              number: {
                value: 40,
              },
              size: {
                value: 3,
              },
            },
          }}
        />
      </div>
    </>
  );
};

export default SimpleParticles;
