import robot from "../images/6.png";

const Community = () => {
  return (
    <section className="mt-10 px-5 lg:px-16 xl:px-24 2xl:px-40 community-bg">
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-10 pt-10 xl:pt-24">
        <div className="text-center self-center">
          <h1 className="text-[#212529] font-bold text-4xl md:text-5xl font-simple">
            Join The SolMekkas Community
          </h1>
          <p className="font-simple my-5 font-medium text-lg">
            Our Discord has a growing community! If you want to keep up on all
            things SolMekkas and communicate with others of the same interest.
            Join the community to get the latest updates on release dates,
            giveaways, collaborations and everything to do with SolMekkas!
          </p>
          <a
            rel="noopener noreferrer"
            href="https://discord.com/invite/wddfVMxweH"
            target="_blank"
          >
            <button className="text-white font-simple font-medium py-3 px-5 bg-[#bc9f27] rounded-md transition duration-500 ease-in-out hover:bg-[#4f7aae]">
              Join Our Discord
            </button>
          </a>
        </div>
        <div>
          <img src={robot} alt="robot" className="object-fill mx-auto" />
        </div>
      </div>
    </section>
  );
};

export default Community;
