import { useEffect, useState } from "react";
import styled from "styled-components";
import Countdown from "react-countdown";
import { CircularProgress, Snackbar } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import * as anchor from "@project-serum/anchor";
import { LAMPORTS_PER_SOL } from "@solana/web3.js";
import { useAnchorWallet } from "@solana/wallet-adapter-react";
import {
  WalletDialogButton,
  WalletDisconnectButton,
} from "@solana/wallet-adapter-material-ui";
import logo from "../images/logot.png";
import logo1 from "../images/logo.png";
import home from "../images/home.png";
import { BsInstagram } from "react-icons/bs";
import { BsTwitter } from "react-icons/bs";
import { FaDiscord } from "react-icons/fa";
import gif from "../images/gif.gif";
import {
  CandyMachine,
  awaitTransactionSignatureConfirmation,
  getCandyMachineState,
  mintOneToken,
} from "./candy-machine";
import { animateScroll as scroll } from "react-scroll";
import { FiArrowUp } from "react-icons/fi";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import SimpleParticles from "../components/SimpleParticles";
import RoadMap from "../sections/RoadMap";
import Teams from "../sections/Teams";
import Community from "../sections/Community";
import Footer from "../components/Footer";
import "../styles/output.css";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: "transparent",
      border: "2px solid #ffff",
      padding: "0.7rem 2rem",
      "&:hover": {
        backgroundColor: "#c2af58",
        border: "2px solid #c2af58",
      },
    },
  })
);

const CounterText = styled.span``; // add your styles here

export interface HomeProps {
  candyMachineId: anchor.web3.PublicKey;
  config: anchor.web3.PublicKey;
  connection: anchor.web3.Connection;
  startDate: number;
  treasury: anchor.web3.PublicKey;
  txTimeout: number;
}

const Home = (props: HomeProps) => {
  const classes = useStyles();
  const [visible, setVisible] = useState(false);
  const [balance, setBalance] = useState<number>();
  const [isActive, setIsActive] = useState(false);
  const [isSoldOut, setIsSoldOut] = useState(false);
  const [isMinting, setIsMinting] = useState(false);

  const [itemsAvailable, setItemsAvailable] = useState(0);
  const [itemsRedeemed, setItemsRedeemed] = useState(0);
  const [itemsRemaining, setItemsRemaining] = useState(0);

  const [alertState, setAlertState] = useState<AlertState>({
    open: false,
    message: "",
    severity: undefined,
  });

  const [startDate, setStartDate] = useState(new Date(props.startDate));

  const wallet = useAnchorWallet();
  const [candyMachine, setCandyMachine] = useState<CandyMachine>();

  const refreshCandyMachineState = () => {
    (async () => {
      if (!wallet) return;

      const {
        candyMachine,
        goLiveDate,
        itemsAvailable,
        itemsRemaining,
        itemsRedeemed,
      } = await getCandyMachineState(
        wallet as anchor.Wallet,
        props.candyMachineId,
        props.connection
      );

      setItemsAvailable(itemsAvailable);
      setItemsRemaining(itemsRemaining);
      setItemsRedeemed(itemsRedeemed);

      setIsSoldOut(itemsRemaining === 0);
      setStartDate(goLiveDate);
      setCandyMachine(candyMachine);
    })();
  };

  const onMint = async () => {
    try {
      setIsMinting(true);
      if (wallet && candyMachine?.program) {
        const mintTxId = await mintOneToken(
          candyMachine,
          props.config,
          wallet.publicKey,
          props.treasury
        );

        const status = await awaitTransactionSignatureConfirmation(
          mintTxId,
          props.txTimeout,
          props.connection,
          "singleGossip",
          false
        );

        if (!status?.err) {
          setAlertState({
            open: true,
            message: "Congratulations! Mint succeeded!",
            severity: "success",
          });
        } else {
          setAlertState({
            open: true,
            message: "Mint failed! Please try again!",
            severity: "error",
          });
        }
      }
    } catch (error: any) {
      // TODO: blech:
      let message = error.msg || "Minting failed! Please try again!";
      if (!error.msg) {
        if (error.message.indexOf("0x138")) {
        } else if (error.message.indexOf("0x137")) {
          message = `SOLD OUT!`;
        } else if (error.message.indexOf("0x135")) {
          message = `Insufficient funds to mint. Please fund your wallet.`;
        }
      } else {
        if (error.code === 311) {
          message = `SOLD OUT!`;
          setIsSoldOut(true);
        } else if (error.code === 312) {
          message = `Minting period hasn't started yet.`;
        }
      }

      setAlertState({
        open: true,
        message,
        severity: "error",
      });
    } finally {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
      setIsMinting(false);
      refreshCandyMachineState();
    }
  };

  useEffect(() => {
    (async () => {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
    })();
  }, [wallet, props.connection]);

  useEffect(refreshCandyMachineState, [
    wallet,
    props.candyMachineId,
    props.connection,
  ]);

  const toggleVisible = () => {
    const scrolled = document.documentElement.scrollTop;
    if (scrolled > 250) {
      setVisible(true);
    } else if (scrolled <= 250) {
      setVisible(false);
    }
  };

  window.addEventListener("scroll", toggleVisible);

  return (
    <>
      <SimpleParticles />
      {/* -------------------------------HERO SECTION----------------------------------- */}
      <section className="h-screen bg-black px-5 lg:px-12 xl:px-16 2xl:px-40 z-10">
        <nav className="lg:flex lg:justify-between lg:items-center py-10">
          <div className="flex justify-center items-center">
            <img
              src={logo}
              alt="logo"
              className="object-fill h-[50px] sm:h-[70px] lg:h-[65px] mr-5"
            />
            <img
              src={logo1}
              alt="logo written"
              className="object-fill h-[50px] sm:h-[70px] lg:h-[65px]"
            />
          </div>
          <div className="flex justify-center items-center mt-10 lg:mt-0">
            <a
              rel="noopener noreferrer"
              target="_blank"
              href="https://www.instagram.com/solmekkas/?utm_medium=copy_link"
              className="bg-[#c2af58] p-3 mr-4 transition duration-500 ease-in-out transform hover:-translate-y-1 hover:bg-[#4f7aae]"
            >
              <BsInstagram className="text-xl" />
            </a>
            <a
              rel="noopener noreferrer"
              target="_blank"
              href="https://mobile.twitter.com/SolMekkas"
              className="bg-[#c2af58] p-2.5 mr-4 transition duration-500 ease-in-out hover:bg-[#4f7aae] transform hover:-translate-y-1"
            >
              <BsTwitter className="text-2xl text-[#081026]" />
            </a>
            <a
              rel="noopener noreferrer"
              target="_blank"
              href="https://discord.com/invite/wddfVMxweH"
              className="bg-[#c2af58] p-2.5 transition duration-500 ease-in-out hover:bg-[#4f7aae] transform hover:-translate-y-1 mr-10 sm:mr-20"
            >
              <FaDiscord className="text-2xl text-[#081026]" />
            </a>

            {!wallet ? (
              <WalletDialogButton className={classes.root}>
                Connect Wallet
              </WalletDialogButton>
            ) : (
              <WalletDisconnectButton startIcon={null} className={classes.root}>
                Wallet Connected
              </WalletDisconnectButton>
            )}
          </div>
        </nav>

        <div className="absolute bottom-0 left-[50%] transform translate-x-[-50%] mt-20 lg:mt-0">
          <img src={home} alt="home" className="object-fill mx-auto" />
        </div>
      </section>

      <div className="bg-[#1e1e24]">
        {/* --------------------------------------MEKAA SECTION-------------------------------------- */}
        <section className="py-16 px-5 lg:px-12 xl:px-16 2xl:px-40">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-10">
            <div className="z-10">
              <img
                src={gif}
                alt="avatar gif"
                className="object-fill lg:h-full"
              />
            </div>
            <div className="bg-[#ececec] flex text-center p-5">
              <div className="m-auto">
                <h1 className="text-[#212529] text-4xl lg:text-5xl mt-8">
                  Buy a Mekka
                </h1>
                <p className="mt-8 md:text-lg">
                  SolMekkas is a collection of 9,000 high-quality NFTs—These
                  unique digital assets are based on the Solana blockchain (NO
                  GAS WAR!). Your Mekka is completely unique and holding
                  multiple will make you eligible for future airdrops in the
                  SolMekka community. Addresses that are whitelisted will be
                  able to engage with the presale button on the date indicated
                  by creators. A max of 6 Mekkas per address will be allowed for
                  mint.
                </p>
                <button className="rounded-full text-white md:text-xl tracking-wider px-20 py-3 bg-[#c2af58] transition duration-500 ease-in-out hover:bg-[#4f7aae] hover:scale-110 mt-5">
                  mint
                </button>
                <button
                  disabled={isSoldOut || isMinting || !isActive}
                  onClick={onMint}
                  className="rounded-full text-white md:text-xl tracking-wider px-14 py-3 bg-[#4f7aae] transition duration-500 ease-in-out hover:bg-[#c2af58] hover:scale-110 mt-5 block mx-auto"
                >
                  {isSoldOut ? (
                    "SOLD OUT"
                  ) : isActive ? (
                    isMinting ? (
                      <CircularProgress />
                    ) : (
                      "pre sale"
                    )
                  ) : (
                    <Countdown
                      date={startDate}
                      onMount={({ completed }) =>
                        completed && setIsActive(true)
                      }
                      onComplete={() => setIsActive(true)}
                      renderer={renderCounter}
                    />
                  )}
                </button>
                <div className="flex justify-center items-center mt-10">
                  <button className="rounded-full text-xl text-white bg-[#212529] hover:bg-[#4f7aae] px-3">
                    -
                  </button>
                  <p className="text-xl mx-3">1</p>
                  <button className="rounded-full text-lg text-white bg-[#212529] hover:bg-[#4f7aae] px-3">
                    +
                  </button>
                </div>
              </div>
            </div>
          </div>
        </section>

        <RoadMap />
        <Teams />
        <Community />
        <Footer />
      </div>

      {visible && (
        <button
          onClick={() => scroll.scrollToTop()}
          className="bg-white rounded-full p-3 fixed bottom-16 right-8 shadow-lg text-[#c2af58] transition duration-500 ease-in-out hover:bg-[#c2af58] hover:text-white"
        >
          <FiArrowUp className=" text-2xl font-bold" />
        </button>
      )}

      <Snackbar
        open={alertState.open}
        autoHideDuration={6000}
        onClose={() => setAlertState({ ...alertState, open: false })}
      >
        <Alert
          onClose={() => setAlertState({ ...alertState, open: false })}
          severity={alertState.severity}
        >
          {alertState.message}
        </Alert>
      </Snackbar>
    </>
  );
};

interface AlertState {
  open: boolean;
  message: string;
  severity: "success" | "info" | "warning" | "error" | undefined;
}

const renderCounter = ({ days, hours, minutes, seconds, completed }: any) => {
  return (
    <CounterText>
      {hours + (days || 0) * 24} hours, {minutes} minutes, {seconds} seconds
    </CounterText>
  );
};

export default Home;
