import logo from "../images/logot.png";
import logo1 from "../images/logo.png";
import { BsInstagram } from "react-icons/bs";
import { BsTwitter } from "react-icons/bs";
import { FaDiscord } from "react-icons/fa";

const Footer = () => {
  return (
    <footer className="lg:flex lg:justify-between lg:items-center py-10 px-5 lg:px-12 xl:px-24 2xl:px-40">
      <div className="flex justify-center items-center">
        <img
          src={logo}
          alt="logo"
          className="object-fill h-[50px] mr-10 xl:mr-20"
        />
        <img src={logo1} alt="logo written" className="object-fill h-[50px]" />
      </div>
      <div className="flex justify-center items-center mt-5 lg:mt-0">
        <a
          rel="noopener noreferrer"
          target="_blank"
          href="https://www.instagram.com/solmekkas/?utm_medium=copy_link"
          className="p-3.5 mr-4 rounded-full border-2 border-white transition duration-500 ease-in-out transform hover:-translate-y-1 hover:bg-[#c2af58] hover:border-[#c2af58]"
        >
          <BsInstagram className="text-xl text-white" />
        </a>
        <a
          rel="noopener noreferrer"
          target="_blank"
          href="https://mobile.twitter.com/SolMekkas"
          className="p-3 mr-4 rounded-full border-2 border-white transition duration-500 ease-in-out hover:bg-[#c2af58] hover:border-[#c2af58] transform hover:-translate-y-1"
        >
          <BsTwitter className="text-2xl text-white" />
        </a>
        <a
          rel="noopener noreferrer"
          target="_blank"
          href="https://discord.com/invite/wddfVMxweH"
          className="p-3 rounded-full border-2 border-white transition duration-500 ease-in-out hover:bg-[#c2af58] hover:border-[#c2af58] transform hover:-translate-y-1 lg:mr-16"
        >
          <FaDiscord className="text-2xl text-white" />
        </a>
        <p className="text-lg text-white font-simple hidden lg:block">
          &copy; {new Date().getFullYear()}. All rights reserved by <br />
          SolMekkas
        </p>
      </div>
      <p className="text-lg text-white font-simple text-center mt-5 lg:hidden">
        &copy; {new Date().getFullYear()}. All rights reserved by SolMekkas
      </p>
    </footer>
  );
};

export default Footer;
